<template>
  <div class="container" style="margin-top:100px">
    <h1>{{ msg }}</h1>
    <h2 v-if="isTrue">{{ msg }}  .</h2>
    <h2 v-if="!isTrue">{{ msg }}  .</h2>
    <p v-if="isTrue">{{ msg }}   .</p>
    <router-link v-bind:to="{ name: 'Main' }">
      <button class="btn btn--back"> </button>
    </router-link>
    <router-link v-if="isTrue" v-bind:to="{ name: 'InputEmail' }">
      <button class="btn btn--back">  ?</button>
    </router-link>
  </div>
</template>

<script>
export default {
  data: () => {
    return {
      msg: "",
      isTrue: false
    };
  },
  mounted() {
    this.msg = this.$route.params.msg;
    // this.msg = " ";
    if (this.msg === "" || this.msg === " ") {
      this.isTrue = true;
    }
  }
};
</script>

<style>
button {
  margin: 5px 0;
}
</style>
