<template>
  <div>
    <div class="input-with-label" style="margin-top:100px;">
      <input
        v-model="password"
        :type="passwordType"
        id="password"
        placeholder=" ."
      />
      <label for="password"> </label>
      <div class="error-text" v-if="error.password != false">
        {{ error.password }}
      </div>
    </div>
    <button class="btn btn--back" v-on:click="withdraw()"> </button>
  </div>
</template>

<script>
import http from "../../../http-common";

export default {
  data: () => {
    return {
      email: "aa@aa.com",
      password: "",
      error: {
        password: false
      },
      isSubmit: false,
      passwordType: "password"
    };
  },
  methods: {
    withdraw() {
      if (confirm("?")) {
        http
          .delete("/user/withdraw", null, {
            params: {
              email: this.email,
              password: this.password
            }
          })
          .then(res => {
            if (res.data == "success") {
              this.$router.push({
                name: "SuccessPage",
                params: {
                  msg: ""
                }
              });
            } else {
              this.error.password = "  .";
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  }
};
</script>

<style></style>
