<template>
  <div class="wrapC" style="margin-top : 30px">
    <div>
      <h1></h1>
      <div class="input-with-label">
        <input
          v-model="nickname"
          id="nickname"
          placeholder=" ."
          type="text"
          style="disabled:true;"
          :readonly="isKakao"
        />
        <label for="nickname"></label>
      </div>

      <div class="input-with-label">
        <input v-model="email" id="email" placeholder=" ." type="text" />
        <label for="email"></label>
        <!-- <button @click="checkEmail()" :disabled="!duplicateEmail"> </button> -->
        <div class="error-text" v-if="error.email">{{ error.email }}</div>
      </div>

      <div class="input-with-label">
        <input
          v-model="password"
          type="password"
          id="password"
          placeholder=" ."
          :readonly="isKakao"
        />
        <label for="password"></label>
        <div class="error-text" v-if="password != 0 && error.password">{{ error.password }}</div>
      </div>

      <div class="input-with-label">
        <input
          v-model="passwordConfirm"
          :type="passwordConfirmType"
          id="password-confirm"
          placeholder="  ."
          :readonly="isKakao"
        />
        <label for="password-confirm"> </label>
        <div
          class="error-text"
          v-if="passwordConfirm != 0 && password != passwordConfirm"
        >{{ error.passwordConfirm }}</div>
      </div>
    </div>
    <div>
      <label>
        <input v-model="isTerm" @click="isTerm = !isTerm" type="checkbox" id="term" />
        <span> .</span>
      </label>

      <span @click="termPopup = !termPopup"></span>
      <div v-if="termPopup == true">""</div>
      <router-link v-bind:to="{ name: 'CompleteJoin' }">
        <button
          class="btn btn--back"
          v-on:click="join()"
          :disabled="!isSubmit"
          :class="{ disabled: !isSubmit }"
        > </button>
      </router-link>
    </div>
  </div>
</template>

<script>
import "../../assets/css/style.scss";
import "../../assets/css/user.scss";
import PasswordValidator from "password-validator";
import * as EmailValidator from "email-validator";
import UserApi from "../../apis/UserApi";

export default {
  data: () => {
    return {
      email: "",
      password: "",
      passwordConfirm: "",
      nickname: "",
      isTerm: false,
      isLoading: false,
      error: {
        email: false,
        password: false,
        nickname: false,
        passwordConfirm: false,
        term: false
      },
      isSubmit: false,
      duplicateEmail: false,
      passwordType: "password",
      passwordConfirmType: "password",
      passwordSchema: new PasswordValidator(),
      termPopup: false,
      isKakao: false
    };
  },
  created() {
    this.component = this;
    console.log(this.$route.params.nickname);
    if (
      !(
        this.$route.params.nickname == "undefined" ||
        this.$route.params.nickname == null ||
        this.$route.params.nickname == ""
      )
    ) {
      this.nickname = this.$route.params.nickname;
      this.password = "kakao4312!@#$";
      this.passwordConfirm = "kakao4312!@#$";
      this.isKakao = true;
    }
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  watch: {
    email: function(v) {
      this.checkForm();
    },
    password: function(v) {
      this.checkForm();
    },
    isTerm: function(v) {
      this.checkForm();
    }
  },
  methods: {
    checkForm() {
      if (this.email.length > 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;

      if (
        this.password.length > 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",  8  .";
      else this.error.password = false;

      let isSubmit = false;
      if (this.email.length > 0 && this.password.length > 0) isSubmit = true;

      let duplicateEmail = true;
      if (this.error.email) duplicateEmail = false;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
      this.duplicateEmail = duplicateEmail;
    },
    join() {
      if (this.isSubmit) {
        let { email, nickname, password } = this;
        let data = {
          email,
          nickname,
          password
        };
        this.isSubmit = false;

        if (!this.isTerm || this.passwordConfirm != this.password) {
          if (!this.isTerm) this.error.term = "  .";
          else this.error.term = false;

          if (this.passwordConfirm != this.password)
            this.error.passwordConfirm = "  .";
          else this.error.passwordConfirm = false;

          this.isSubmit = true;
        } else {
          UserApi.requestSignup(
            data,
            res => {
              console.log(res);
              alert(" mail .");
              if (res == "success")
                this.$router.push({
                  name: "SuccessPage",
                  params: {
                    msg: ""
                  }
                });

              this.isSubmit = true;
            },
            error => {
              console.log(error);
              this.isSubmit = true;
            }
          );
        }
      }
    },
    checkEmail() {
      if (this.duplicateEmail) {
        let { email } = this;
        let data = {
          email
        };

        this.duplicateEmail = false;

        UserApi.requestCheckEmail(
          data,
          res => {
            console.log(res);
            if (res == "success") alert("  email.");
            else alert("  email.");
            this.duplicateEmail = true;
          },
          error => {
            console.log(error);
            this.duplicateEmail = true;
          }
        );
      }
    }
  }
};
</script>
<style scoped>
.wrapC {
}
.input-with-label input,
label {
}
</style>
